<template>
  <div class="businessContent">
    <section class="top_Div">
      <tab :yearAchtive="yearAchtive" @onclick="selectTime" :data='this.getRespone.tabJson'/>
      <ve-line :data="chartData" :extend="extend" :settings="chartSettings"></ve-line>
    </section>
    <section class="business">
      <div>{{currentTime}}</div>
      <div class="bussinessMoney">
        <p>{{money}}</p>
        <p>营业额（元）</p>
      </div>
    </section>
    <imgTable :data='this.getRespone.businessJson'/>
  </div>
</template>

<script>
import tab from "@/components/tab";
import imgTable from '@/components/imgTable'
import veLine from "@/components/veLine";

export default {
  data() {
    this.extend = {
      series: {
        label: {
          normal: {
            show: false
          }
        }
      },
      xAxis: {
        type: "category",
        boundaryGap: false
      }
    };
    this.chartSettings = {
      name: "",
      area: true
    };
    return {
      yearAchtive: 1,
      chartData: this.getRespone.json,
      currentTime: "",
      money: ""
    };
  },
  mounted() {
    this.selectTime(1);
  },
  methods: {
    selectTime(e) {
      var year = this.util.getTime(new Date()).substring(0,4);
      var month = this.util.getTime(new Date()).substring(5, 7);
      var day = this.util.getTime(new Date()).substring(8,10);
      if (e === 1) {
        this.currentTime = day + "日";
        this.money = this.util.setMoney("5862455");
      } else if (e === 2) {
        if(month % 2 !== 0){
          this.currentTime = month + "月1日 ~" + month + '月30日';          
        } else {
          this.currentTime = month + "月1日 ~" + month + '月31日';
        }
        this.money = this.util.setMoney("5862455");
      } else {
        this.currentTime = year + '年'
        this.money = this.util.setMoney("5862455");
      }
      this.yearAchtive = e;
    }
  },
  components: {
    tab,
    veLine,
    imgTable
  }
};
</script>

<style lang="scss" scoped>
.businessContent {
  .top_Div {
    background-color: $fc;
    padding: 0.8rem 0.5rem 0rem 0.5rem;
  }
  .imgTable {
    @include grid(2, 2);
    .tableContent {
      @include fr(center);
      @include sc(0.8rem, $line);
      padding: 0.8rem;
      background: $fc;
      .icoDiv {
        @include wh(2.5rem, 2.5rem);
        @include fc(center);
        border-radius: 5rem;
        margin-right: 0.5rem;
        img {
          @include wh(1.5rem, 1.5rem);
        }
      }
      .rightInfo p:first-child {
        font-weight: bold;
        @include sc(0.95rem, $fColor);
      }
    }
  }

  .business {
    background-color: $fc;
    padding: 0rem 1rem;
    div:first-child {
      text-align: left;
    }
    .bussinessMoney p:first-child {
      @include sc(1.5rem, $blue);
      font-weight: bold;
      margin-top: 1rem;
    }
    .bussinessMoney p:last-child {
      @include sc(0.92rem, $line);
      padding: 0.4rem;
    }
  }
}
</style>