<template>
  <section class="header">
    <div class="headLine">
      <p
        v-for="(item,index) in data"
        :key="index"
        @click="selectTime(index+1)"
        :class="yearAchtive == index+1 ? 'active':''"
      >{{item.name}}</p>
    </div>
  </section>
</template>

<script>
export default {
  props: ["yearAchtive", "data"],
  methods: {
    selectTime(id) {
      this.$emit("onclick", id);
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  @include wh(100%, auto);
  .headLine {
    @include fr(center);
    // @include wh(51.5%,auto);
    margin: 0.8rem 4rem;
    border-radius: 3rem;
    box-shadow: 0rem 0.3rem 0.8rem $Tline;
    p {
      padding: 0.5rem 1.5rem;
      @include sc(0.95rem, $fColor);
      flex: 1;
    }
    p:first-child {
      border-top-left-radius: 3rem;
      border-bottom-left-radius: 3rem;
    }
    p:last-child {
      border-top-right-radius: 3rem;
      border-bottom-right-radius: 3rem;
    }
    .active {
      background-color: $blue;
      color: $fc;
    }
  }
}
</style>